@import url(https://fonts.googleapis.com/css2?family=Kanit:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0}body{font-family:"Kanit", sans-serif;background-color:#2e3440;color:#e5e9f0;display:flex;flex-direction:column;align-items:center;text-align:center}h1{margin:2rem 0;font-weight:400}main{display:flex;flex-direction:column;border:7px groove #88c0d0;align-items:center}section{display:flex;width:70rem;margin-bottom:2rem}.button{border:1px solid #e5e9f0;text-transform:uppercase;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:0.3rem 1.5rem;background-color:#a3be8c;border-color:#a3be8c;color:#2e3440;font-weight:500;font-family:"Kanit", sans-serif;border-radius:0.25rem;font-size:1rem;margin-right:0.25rem}.button:hover{background-color:#88c0d0;border-color:#88c0d0;cursor:pointer}.button:focus{outline:none;border-color:#88c0d0;background-color:#88c0d0;color:#e5e9f0}.button.addTask{font-size:3rem;padding:0 0 0.4rem 0;border-radius:50%;width:2.5rem;height:2.5rem;line-height:1rem;margin-top:2rem}.button.moveTask{border-color:#8fbcbb;background-color:#8fbcbb;font-size:1.5rem;padding:0.5rem 0.5rem;margin-bottom:0;line-height:1rem}.button.delete{background-color:#bf616a;border-color:#bf616a}.button.delete:hover{background-color:#b48ead;border-color:#bf616a}@media only screen and (max-width: 75rem){section{width:100%;flex-direction:column}}

.statusLine{width:100%;margin:0 1rem}.statusLine h3{font-weight:500;margin:2rem 0}@media only screen and (max-width: 75rem){.statusLine{width:auto;width:initial}}

.task{padding:1rem;margin-bottom:0.5rem;display:flex;align-items:flex-end;border:4px ridge #e5e9f0;border-radius:0.25rem}.task.collapsedTask .moveTask{margin-bottom:0.5rem;font-size:1.5rem;padding:0.3rem 0.5rem}.task form{display:flex;flex-direction:column;text-align:center;align-items:center;margin:0 0.25rem}.task form .input{background-color:transparent;border:1px solid transparent;border-radius:0.25rem;color:#e5e9f0;text-align:center;resize:none;display:flex;font-family:"Kanit", sans-serif}.task form .input:hover{cursor:pointer;border:1px solid #e5e9f0}.task form .input:focus{outline:none;border-color:#e5e9f0;color:#e5e9f0}.task form .input::-webkit-input-placeholder{color:#e5e9f0}.task form .input:-ms-input-placeholder{color:#e5e9f0}.task form .input::placeholder{color:#e5e9f0}.task form .title{margin-bottom:0.5rem;padding:0.5rem 0;width:100%;font-size:1.1rem}.task form .title:hover{cursor:text}.task form .description{margin-bottom:0.5rem;padding:0.5rem;width:100%;font-size:1.1rem}.task form .description:hover{cursor:text}.task form .urgencyLabels{margin-bottom:1.5rem;display:flex;flex-direction:row}.task form .urgencyLabels label{color:#2e3440;padding:0.05rem 0.5rem;margin:0 0.25rem;text-transform:uppercase;border-radius:0.25rem}.task form .urgencyLabels label .icon{font-size:1.25rem;margin-right:0.25rem;transition:all 0.4s ease-in-out}.task form .urgencyLabels label .icon.hide{display:none}.task form .urgencyLabels label .icon.show{display:inline-block}.task form .urgencyLabels label input{display:none}.task form .urgencyLabels label.low{background-color:#81a1c1}.task form .urgencyLabels label.low:hover{cursor:pointer;background-color:#88c0d0;border-color:#88c0d0}.task form .urgencyLabels label.medium{background-color:#ebcb8b}.task form .urgencyLabels label.medium:hover{cursor:pointer;background-color:#88c0d0;border-color:#88c0d0}.task form .urgencyLabels label.high{background-color:#b48ead}.task form .urgencyLabels label.high:hover{cursor:pointer;background-color:#88c0d0;border-color:#88c0d0}.task form .urgencyLabels label.selected{font-weight:700}.task form.collapsed{flex-direction:row;align-content:center}.task form.collapsed .title{margin:0}.task form.collapsed .title:hover{border-color:transparent;cursor:default}.task form.collapsed .urgencyLabels{margin-bottom:0;margin-right:1rem}.task form.collapsed .description,.task form.collapsed label{display:none}.task form.collapsed label.selected{display:inline-block;font-weight:500}.task form.collapsed .button{padding:0.025rem 0.5rem}.task form.collapsed .button.moveTask{background-color:red}.task form.collapsed:hover .button{display:inline-block}

